button {
  &.normal {
    font-size: 14px;
    font-weight: 600;
    padding: 15px 30px;
    color: #000;
    background-color: #fff;
    border-radius: .25rem;
    cursor: pointer;
    border: none;
    outline: none;
    transition: .2s; }
  &.white {
    font-size: .8125rem;
    font-weight: 600;
    padding: 11px 18px;
    color: #fff;
    background-color: transparent;
    cursor: pointer;
    border: 1px solid #fff;
    outline: none;
    transition: .2s; } }
