.product {
  padding: 40px 80px;
  text-align: center;
  &-wrapper {
    display: flex;
    justify-content: space-between;
    padding-top: 20px;
    flex-wrap: wrap; }
  &__card {
    width: 23%;
    min-width: 15.625rem;
    padding: 10px 12px;
    border: 1px solid #cce7d0;
    border-radius: 25px;
    cursor: pointer;
    box-shadow: 20px 20px 30px rgba(0, 0, 0, 0.02);
    margin: 15px 0px;
    transition: .2s ease;
    position: relative;
    &:hover {
      box-shadow: 20px 20px 30px rgba(0, 0, 0, 0.06); }
    img {
      width: 100%;
      border-radius: 20px; } }
  &__descr {
    text-align: start;
    padding: 10px 0;
    span {
      color: #606063;
      font-size: .75rem; }
    h5 {
      padding-top: 7px;
      color: #1a1a1a;
      font-size: 14px; }
    i {
      font-size: .75rem;
      color: rgb(243, 181, 25); } }
  &__price {
    padding-top: 7px;
    font-size: 15px;
    font-weight: 700;
    color: $--primary-color; }
  .cart {
    width: 40px;
    height: 40px;
    line-height: 40px;
    border-radius: 50%;
    background-color: #e8f6ea;
    font-weight: 500;
    border: 1px solid #cce7d0;
    color: $--primary-color;
    position: absolute;
    bottom: 15px;
    right: 10px;
    &:hover {
      background-color: #cad6cc; } } }

/* Single Product Page */
.productdetails {
  padding: 40px 80px;
  display: flex;
  margin-top: 20px;
  .single-pro-image {
    width: 40%;
    margin-right: 50px; }
  .single-pro-details {
    width: 50%;
    padding-top: 30px;
    h4 {
      padding: 40px 0 20px 0; }
    h2 {
      font-size: 1.625rem; }
    select {
      display: block;
      padding: 5px 10px;
      margin-bottom: 10px;
      outline: none; }
    input {
      width: 50px;
      height: 2.9375rem;
      padding-left: 10px;
      font-size: 1rem;
      margin-right: 10px;
      outline: none; }
    button {
      background-color: $--primary-color;
      color: #fff; }
    span {
      line-height: 25px; } } }


.small-img-group {
  display: flex;
  justify-content: space-between; }

.small-img-col {
  flex-basis: 24%;
  cursor: pointer; }
