.banner {
  margin: 40px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-image: url('../../assets/images/banner/b2.jpg');
  width: 100%;
  height: 40vh;
  background-size: cover;
  background-position: center;
  h4 {
    color: #fff;
    font-size: 1rem; }
  h2 {
    font-size: 30px;
    padding: 10px 0;
    color: #fff;
    span {
      color: #ef3636; } }
  &__btn:hover {
    background: $--primary-color;
    color: #fff; } }

/* Small Banners */
.sm-banner {
  padding: 40px 80px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  .banner-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    background-image: url('../../assets/images/banner/b17.jpg');
    min-width: 43vw;
    height: 50vh;
    background-size: cover;
    background-position: center;
    padding: 30px;
    h4 {
      color: #fff;
      font-size: 20px;
      font-weight: 300; }
    h2 {
      font-size: 1.75rem;
      font-weight: 800;
      color: #fff; }
    span {
      color: #fff;
      font-size: 14px;
      font-weight: 500;
      padding-bottom: 15px; }
    &__btn:hover {
      background: $--primary-color;
      border: 1px solid $--primary-color; }
    &:nth-child(2) {
      background-image: url('../../assets/images/banner/b10.jpg'); } } }

/* TEXT BANNERS */
.txt-banner {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0 80px;
  .banner-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    background-image: url('../../assets/images/banner/b7.jpg');
    min-width: 30%;
    height: 30vh;
    background-size: cover;
    background-position: center;
    padding: 20px;
    margin-bottom: 20px;
    h2 {
      color: #fff;
      font-size: 1.375rem;
      font-weight: 900; }
    h3 {
      color: #ec544e;
      font-size: 15px;
      font-weight: 800; }
    &:nth-child(2) {
      background-image: url('../../assets/images/banner/b4.jpg'); }
    &:last-child {
      background-image: url('../../assets/images/banner/b18.jpg'); } } }
