.contact-details {
  padding: 40px 80px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  .details {
    width: 40%;
    span {
      text-transform: uppercase;
      font-size: .75rem; }
    h2 {
      font-size: 1.625rem;
      line-height: 2.1875rem;
      padding: 20px 0; }
    h3 {
      font-size: 16px;
      padding-bottom: 15px; }
    li {
      display: flex;
      padding: 10px 0;
      i {
        font-size: .875rem;
        padding-right: 1.375rem; }
      p {
        margin: 0;
        font-size: .875rem; } } }
  .map {
    width: 55%;
    height: 400px;
    iframe {
      width: 100%;
      height: 100%; } } }

.contact-form {
  width: 80%;
  margin: 30px auto;
  display: flex;
  justify-content: space-evenly;
  padding: 30px 1.25rem;
  border: 1px solid #e1e1e1;
  border-radius: 20px;
  form {
    width: 65%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    .input-group {
      width: 100%; }
    h2 {
      margin-bottom: 20px; }
    input,
    textarea {
      width: 100%;
      padding: 15px;
      outline: none;
      margin-bottom: 20px;
      border: 1px solid #e1e1e1; }
    button {
      background-color: $--primary-color;
      color: #fff; } }
  .people {
    display: flex;
    flex-direction: column;
    &-box {
      padding: 0 5px;
      display: flex;
      align-items: center;
      flex: 1;
      img {
        width: 65px;
        height: 65px;
        object-fit: cover;
        margin-right: 15px; }
      p {
        margin: 0;
        font-size: .8125rem;
        line-height: 1.5625rem;
        span {
          display: block;
          font-size: 16px;
          font-weight: 600;
          color: #000; } } } } }
