#cart {
  padding: 40px 80px;
  width: 80%;
  margin: auto;
  table {
    width: 100%;
    border-collapse: collapse; }
  .cart-info {
    display: flex;
    flex-wrap: wrap; }
  th {
    text-align: left;
    padding: 10px 5px;
    color: #fff;
    background-color: rgb(6, 85, 103);
    font-weight: normal; }
  td {
    padding: 10px 5px;
    input {
      width: 50px;
      height: 30px;
      padding: 5px;
      outline: none; }
    a {
      color: rgb(6, 85, 103);
      font-size: 12px;
      transition: .3s;
      &:hover {
        color: red; } }
    img {
      width: 120px;
      height: 120px;
      display: inline-block;
      margin-right: 10px; }
    &:nth-child(3) {
      width: 103px; } }


  .total-price {
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    align-items: flex-end;
    table {
      border-top: 3px solid rgb(6, 85, 103);
      width: 100%;
      max-width: 400px;
      margin-bottom: 20px;
      td:last-child,
      th:last-child {
        text-align: right; }
      tr:last-child {
        margin-top: 50px;
        td {
          background-color: $--primary-color;
          color: #fff; } } }
    button {
      background-color: $--primary-color;
      color: #fff;
      &:hover {
        box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2); } } } }

