.footer {
  padding: 40px 80px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .col {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 20px; }
  &__logo {
    margin-bottom: 30px; }
  h4 {
    font-size: 14px;
    padding-bottom: 20px; }
  p {
    font-size: .8125rem;
    margin: 0 0 8px 0; }
  a {
    font-size: 13px;
    color: #222;
    margin: 0 0 8px 0;
    &:hover {
      color: $--primary-color; } }
  .install {
    .row {
      margin: 10px 0 15px 0;
      img {
        border: 1px solid $--primary-color;
        border-radius: .375rem; } } }
  .copyright {
    width: 100%;
    text-align: center; } }


.follow {
  margin: 20px;
  i {
    color: #465b52;
    padding-right: 4px;
    cursor: pointer;
    &:hover {
      color: $--primary-color; } } }

