#header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 80px;
  background-color: #e3e6f3;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.06);
  z-index: 999;
  position: sticky;
  top: 0;
  left: 0; }

.nav {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  &__item {
    position: relative;
    a {
      font-size: 1rem;
      font-weight: 600;
      color: #1a1a1a;
      transition: .3s ease; } } }

.nav__item a:hover,
.nav__item.active {
  color: #088178; }

.nav__item.active,
.nav__item a:hover {
  &::after {
    content: "";
    width: 45%;
    height: 2px;
    background: #088178;
    position: absolute;
    bottom: -4px;
    left: 2px; } }

#mobile {
  display: none;
  align-items: center; }

#close {
  display: none; }

#lg-bag {
  .cart__num {
    position: absolute;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    top: -10px;
    right: -10px;
    font-size: 9px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $--primary-color;
    color: #fff;
    padding-top: 2px; } }
