.blog {
  padding: 150px 150px 0 150px;
  &-box {
    display: flex;
    align-items: center;
    width: 100%;
    position: relative;
    padding-bottom: 90px;
    h1 {
      position: absolute;
      top: -40px;
      left: 0;
      font-size: 4.375rem;
      font-weight: 700;
      color: #c9cbce;
      margin: 0;
      z-index: -9; }
    .blog-img {
      width: 50%;
      margin-right: 40px;
      img {
        width: 100%;
        height: 300px;
        object-fit: cover; } }
    .blog-details {
      width: 50%;
      a {
        text-transform: uppercase;
        font-size: .6875rem;
        color: #000;
        font-weight: 700;
        position: relative;
        transition: .3s;
        &::after {
          content: "";
          width: 3.125rem;
          height: .0625rem;
          background-color: #000;
          position: absolute;
          top: 4px;
          right: -60px;
          transition: .3s; }
        &:hover {
          color: $--primary-color;
          &::after {
            background-color: $--primary-color; } } } } } }

