.news {
  padding: 40px 80px;
  margin: 40px 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  background-image: url('../../assets/images/banner/b14.png');
  background-repeat: no-repeat;
  background-position: 20% 30%;
  background-color: #041e42;
  h4 {
    font-size: 1.375rem;
    font-weight: 700;
    color: #fff; }
  p {
    font-size: 14px;
    font-weight: 600;
    color: #fff;
    span {
      color: #ffbd27; } }
  input {
    height: 3.125rem;
    padding: 0 1.25em;
    font-size: .875rem;
    width: 100%;
    border: 1p solid transparent;
    border-radius: 4px;
    outline: none;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0; }
  button {
    background-color: $--primary-color;
    color: #fff;
    white-space: nowrap;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0; }
  &__form {
    display: flex;
    width: 40%; } }
