@media (max-width: 1199px) {
  .hero {
    height: 50vh; }
  .feature {
    justify-content: center;
    &__card {
      margin: 15px 30px; } }
  .sm-banner {
    padding: 40px 60px; }
  #contact-details {
    padding: 40px 35px; }
  #contact-form {
    width: 90%;
    form {
      margin-right: 30px;
      h2 {
        font-size: 2rem; } } }
  #cart {
    padding: 40px;
    width: 100%; } }

@media ( max-width: 991px ) {
  .hero {
    height: 60vh;
    padding: 0 40px;
    h2 {
      font-size: 2.5rem; }
    h1 {
      font-size: 2.8125rem; } }
  .product {
    &-wrapper {
      justify-content: center; }
    &__card {
      margin: 15px; } }
  .banner {
    height: 30vh; }
  .sm-banner {
    .banner-box {
      min-width: 100%;
      height: 30vh; } }
  .txt-banner {
    padding: 0 40px;
    .banner-box {
      width: 33%;
      h2 {
        font-size: 1rem; } } }
  .news {
    padding: 30px 50px;
    &__form {
      width: 65%; } }
  .footer {
    p {
      font-size: .6875rem; } }
  .blog {
    padding: 150px 130px 0 90px; }
  #about-head {
    flex-direction: column;
    align-items: flex-start;
    padding: 40px 60px;
    .swiper {
      margin-right: 0px;
      width: 100%;
      margin-bottom: 30px; }
    .about-head__img {
      width: 100%;
      margin-right: 0px;
      margin-bottom: 30px; }
    .about-head__details {
      width: 100%; } }
  #contact-form {
    .people-box {
      flex: .3; }
    input {
      margin-bottom: 15px; } }
  #cart {
    padding: 40px 50px; } }

@media ( max-width: 799px ) {
  #header {
    padding: 20px 60px; }
  #navbar {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    position: fixed;
    top: 0;
    right: -300px;
    height: 100vh;
    width: 300px;
    background-color: #e3e6f3;
    box-shadow: 0 40px 60px rgba(0, 0, 0, 0.1);
    padding: 80px 0 0 10px;
    transition: .3s;
    z-index: 3;
    li {
      margin-bottom: 25px; }
    &.active {
      right: 0px; } }
  #mobile {
    display: flex;
    align-items: center;
    i {
      color: #1a1a1a;
      font-size: 1.5rem;
      padding-left: 20px;
      cursor: pointer; } }
  #close {
    display: initial;
    position: absolute;
    top: 30px;
    left: 30px;
    color: #222;
    font-size: 1.5rem;
    cursor: pointer; }
  #lg-bag {
    display: none; }
  .hero {
    height: 45vh;
    background-position: top 30% right 60%;
    h2 {
      font-size: 2rem; }
    h1 {
      font-size: 2.3rem;
      margin: 0.17em 0;
      line-height: 3rem; }
    p {
      font-size: 0.8125rem; } }
  .feature {
    padding: 15px 80px;
    &__card {
      margin: 15px; } }
  .product {
    padding: 15px 20px;
    h1 {
      font-size: 2.8rem;
      line-height: 2rem; }
    &__card {
      margin: 10px; } }
  .banner {
    height: 25vh;
    h2 {
      font-size: 25px; } }
  .sm-banner {
    padding: 20px 60px; }
  .news {
    &__form {
      width: 75%; } }
  #page-header,
  #page-header.blog-header {
    height: 25vh; }
  #blog {
    padding: 120px 55px 0 40px; }
  #contact-details {
    padding: 30px 20px;
    .details h2 {
      font-size: 1.3rem;
      line-height: 1.5rem; } }
  #contact-form {
    form h2 {
      font-size: 1.6rem;
      line-height: 2.375rem; } }
  #cart {
    padding: 15px 20px; }
  #about-head {
    padding: 40px; } }

@media ( max-width: 599px ) {
  #header {
    padding: 10px 30px; }
  h2 {
    font-size: 2rem; }
  .hero {
    padding: 0 20px;
    background-position: 55%;
    h4 {
      padding-bottom: 0; }
    h2 {
      font-size: 1.75rem;
      line-height: 2.8125rem; }
    h1 {
      font-size: 1.8rem;
      line-height: 2rem; }
    p {
      font-size: 0.7rem; } }
  .feature {
    justify-content: space-between;
    padding: 15px 70px;
    &__card {
      width: 155px;
      margin: 0 0 15px 0; } }
  .product {
    h1 {
      font-size: 1.8rem;
      line-height: 0.3rem; }
    &__card {
      width: 100%; } }
  .banner {
    height: 35vh; }
  .sm-banner {
    .banner-box {
      height: 40vh;
      &:last-child {
        margin-top: 20px; } } }
  .txt-banner {
    padding: 0 20px;
    .banner-box {
      width: 100%; } }
  .news {
    padding: 40px 20px;
    &__form {
      width: 100%; } }
  .footer .copyright {
    text-align: start; }
  .page-header {
    height: 20vh; }
  .productdetails {
    flex-direction: column;
    .single-pro-image {
      width: 100%;
      margin-right: 0px; }
    .single-pro-details {
      width: 100%;
      h4 {
        padding: 18px 0 5px 0; } } }
  #page-header,
  #page-header.blog-header {
    height: 20vh; }
  #blog {
    padding: 95px 10px 0 12px;
    .blog-box {
      h1 {
        font-size: 3.375rem; }
      &__title {
        font-size: .9375rem; } } }
  #about-head {
    padding: 35px;
    .swiper-button-next,
    .swiper-button-prev {
      display: none; } }
  #contact-details {
    flex-direction: column;
    .details {
      width: 100%;
      margin-bottom: 15px; }
    .map {
      width: 100%; } }
  #contact-form {
    flex-direction: column;
    form {
      width: 100%;
      margin-bottom: 30px; }
    .people {
      align-items: center;
      gap: 10px; } } }

@media ( max-width: 469px ) {
  #header {
    padding: 10px; }
  #mobile i {
    font-size: 1.2rem; }
  .hero {
    background-position: 65%;
    h2 {
      font-size: 1.1rem;
      line-height: 1.8rem; }
    h4 {
      font-size: 1rem; }
    h1 {
      font-size: 1.2rem;
      line-height: 1.5rem; }
    p {
      width: 144px; }
    &__btn {
      padding: 11px 72px 14px 2.875rem;
      background-size: 144px;
      font-size: 10px; } }
  .feature {
    padding: 15px 18px;
    justify-content: center;
    &__card {
      width: 130px;
      margin: 0 7px 15px 7px; } }
  .product {
    h1 {
      font-size: 1.4rem; }
    &-wrapper {
      padding-top: 0px; } }
  .banner {
    height: 25vh;
    h2 {
      font-size: .875rem;
      line-height: 1.375rem; }
    &__btn {
      font-size: .75rem; } }
  .sm-banner {
    padding: 20px;
    .banner-box {
      height: 30vh;
      h2 {
        line-height: 2.375rem; } } }
  .news {
    h4 {
      font-size: 1.2rem; }
    p {
      font-size: .75rem; }
    input {
      height: 2.8rem; }
    button {
      font-size: 10px;
      padding: 15px; } }
  .footer {
    justify-content: center;
    align-items: center;
    padding: 40px;
    img {
      width: 50%; }
    .follow {
      margin: 15px; }
    .col {
      min-width: 100%; } }
  .productdetails {
    padding: 10px 30px; }
  #blog {
    padding: 100px 20px 0 20px;
    .blog-box {
      display: flex;
      flex-direction: column;
      align-items: flex-start; }
    .blog-img {
      width: 100%;
      margin-right: 0px;
      margin-bottom: 30px; }
    .blog-details {
      width: 100%;
      p {
        margin: 15px 0 10px 0; } } }
  .about-app {
    h1 {
      font-size: 2.125rem;
      line-height: 2.5rem; } }
  .video {
    width: 90%; }
  #contact-details {
    .map {
      height: 300px; } }
  #contact-form {
    margin: 10px auto;
    form {
      input {
        padding: 10px;
        margin-bottom: 10px; }
      button {
        display: block;
        margin: 0 auto;
        padding: 10px 22px; } } } }
