.about-head {
  padding: 40px 80px;
  display: flex;
  align-items: center;
  .swiper {
    width: 50%;
    height: 450px;
    margin-right: 40px;
    border-radius: 20px;
    &-slide {
      text-align: center;
      font-size: 18px;
      background-color: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        display: block;
        object-fit: cover;
        width: 100%;
        height: 100%; } } }
  &__details {
    width: 50%; } }

.about-app {
  text-align: center;
  .video {
    width: 70%;
    height: 100%;
    margin: 30px auto 0 auto;
    video {
      width: 100%;
      height: 100%;
      border-radius: 1.25rem; } } }
